@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans:800);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}
*:focus {
  outline: none;
}
*:active {
  outline: none;
}

body {
  font-family: 'Alegreya Sans', sans-serif;
}

.container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.menu-btn {
  position: absolute;
  left: 30px;
  top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 50px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  z-index: 2;
}

.menu-btn__burger {
  width: 50px;
  height: 4px;
  background-color: black;
  border-radius: 999px;
  box-shadow: 0 2px 0px rgba(0, 0, 0, 0.75);
  transition: all .5s ease-in-out;
}

.menu-btn__burger::before,
.menu-btn__burger::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 4px;
  background-color: black;
  border-radius: 999px;
  box-shadow: 0 2px 0px rgba(0, 0, 0, 0.75);
  transition: all .3s ease-in-out;
}

.menu-btn__burger::before {
  transform: translateY(-14px);
}

.menu-btn__burger::after {
  transform: translateY(14px);
}

.menu-btn.open .menu-btn__burger {
  box-shadow: none;
  background: transparent;
}

.menu-btn.open .menu-btn__burger::before {
  transform: rotate(45deg) ;
}

.menu-btn.open .menu-btn__burger::after {
  transform: rotate(-45deg) ;
}

nav {
  width: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  transition: .7s;
  z-index: 1;
  background-color: teal;
}
nav.open {
  width: 100%;
}

.mainnav__items {
  flex: 3 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.mainnav__items li a {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  color: black;
}

.socialmedia__items {
  flex: 1 1;
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.socialmedia__items li a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.socialmedia__items li a img {
  width: 25px;
  margin: 0 10px;
}
.header {
  display: flex;
  flex-direction: column;
  background-color: grey;
}

.logo {
  position: absolute;
  top: 15px;
  right: 30px;
  width: 150px;
  z-index: -2;
}

.player {
  width: 100%;
  padding: 5px 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  background-color: silver;
}

.progress__section {
  flex: 1 1;
  display: flex;
  justify-content: center;
  margin: 5px 0;
}
.start-time,
.end-time {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.progress-bar {
  flex-grow: 1;
  background-color: grey;
  margin: 0 20px;
  height: 3px;
  align-self: center;
  border-radius: 999px;
}

.current-progress {
  position: relative;
  width: 70%;
  height: 3px;
  background-color: teal;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border-radius: 999px;
}

.current-progress__outercircle {
  height: 26px;
  width: 26px;
  background-color: darkgoldenrod;
  position: relative;
  left: 10px;
  border-radius: 50%;
  border: teal solid 1px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.current-progress__innercircle {
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background-color: teal;
}

.control-panel__section {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
}

.song-details {
  display: flex;
  align-items: center;
  height: 100%;
  width: 33.33%;
}

.song-details > img {
  width: 80px;
  height: 80px;
  border-radius: 10px;
}

.song-details  div {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  overflow: hidden;
}

.player-settings {
  height: 100%;
  width: 33.33%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.services__section {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 33.33%;
}

* {
  margin: 0;
  text-decoration: none;
}


.site {
  background-color: #f1f2f5;
  min-height: 100vh;
}

